







































































































.note-container {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  box-sizing: border-box;
  z-index: 13;
  width: 100%;
}
.note-action {
  background: #3d3b4a;
  margin: 0.5rem 1rem;
  width: calc(100% - 2rem);
  border-radius: 5px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  bottom: 0;
  position: absolute;
}
.note-action a, .note-action a i {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 auto;
          flex: 1 1 auto;
  text-align: center;
  height: 3rem;
  line-height: 3rem;
  position: relative;
  color: #fff;
  font-size: 1.5rem;
}
.note-editor {
  background: #3d3b4a;
  margin: 0.5rem 1rem;
  width: calc(100% - 2rem);
  bottom: 4rem;
  top: 0;
  padding: 0.5rem;
  box-sizing: border-box;
  position: absolute;
}
.note-content-editor {
  position: absolute;
  bottom: 0.5rem;
  top: 7.5rem;
  /*margin: 0.5rem ;*/
  width: calc(100% - 1rem);
  box-sizing: border-box;
}
.note-content-editor textarea {
  position: absolute;
  width: 100%;
  top: 0;
  bottom: 0;
}




